/* Main navigation stylings */
.nav-link,
.nav-link:hover {
  color: #f5f5f5;
  margin-top: 15px;
}

.nav-link:hover {
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  background-color: #504d4d93;
  color: #f5f5f5;
}

.main-navigation-container {
  border-bottom: 1px solid #6e6e6e;
}

.main-navigation {
  border: 0;
}
/* End of Main navigation stylings */
